.header {
  position: relative;
  z-index: 3;
}
.header__wrap {
  display: flex;
  align-items: center;
}
.header__top {
  padding: 20px 0 22px;
  background-color: #007ac3;
}
.header__top .logo svg {
  width: 214px;
  height: 53px;
}
.header__top .logo-text {
  font-family: 'Cuprum', sans-serif;
  font-size: 15px;
  line-height: 1.4;
  font-weight: 400;
  text-transform: uppercase;
  color: #fff;
  margin-left: 28px;
  margin-top: 9px;
}
.header__top .btn {
  display: flex;
  align-items: center;
  font-size: 13px;
  line-height: 1.2;
  font-weight: 500;
  margin-left: auto;
  padding: 7px 19px;
}
.header__top .btn svg {
  width: 24px;
  height: 24px;
  stroke: #fff;
  stroke-width: 2px;
  stroke-linejoin: round;
  fill: none;
  margin-right: 19px;
}
.header__top .burger {
  display: none;
  width: 24px;
  height: 18px;
  position: relative;
  margin-left: auto;
}
.header__top .burger.active span:nth-of-type(1) {
  bottom: 0;
  transform: rotate(45deg);
}
.header__top .burger.active span:nth-of-type(2) {
  opacity: 0;
}
.header__top .burger.active span:nth-of-type(3) {
  top: 0;
  transform: rotate(-45deg);
}
.header__top .burger span {
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #ea8f00;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  opacity: 1;
  transition: 0.3s;
}
.header__top .burger span:nth-of-type(1) {
  bottom: auto;
}
.header__top .burger span:nth-of-type(3) {
  top: auto;
}
.header__bottom {
  background-color: #03629b;
}
.header__bottom .nav {
  display: flex;
  align-items: center;
  margin-left: -20px;
}
.header__bottom .menu {
  display: flex;
  align-items: center;
  font-family: 'Cuprum', sans-serif;
  padding: 0;
  margin: 0;
  list-style: none;
  color: #fff;
  font-size: 20px;
}
.header__bottom .menu a {
  display: block;
  padding: 13px 20px;
  background-color: transparent;
  transition: 0.3s;
}
.header__bottom .menu a:hover {
  background-color: #aedaf3;
}
.header__bottom .links {
  display: flex;
  align-items: center;
  margin-left: 33px;
}
.header__bottom .links svg {
  width: 24px;
  height: 24px;
  fill: none;
  margin-right: 10px;
}
.header__bottom .links a {
  color: #fff;
  font-size: 13px;
  line-height: 1.6;
}
.header__bottom .links a:nth-of-type(1) {
  position: relative;
  margin-right: 21px;
}
.header__bottom .links a:nth-of-type(1):after {
  content: '';
  position: absolute;
  right: -12px;
  top: 0;
  width: 2px;
  height: 100%;
  background-color: #fff;
}
.header__bottom .links a.uppercase {
  text-transform: uppercase;
}
.header__bottom .lang {
  display: flex;
  align-items: center;
  margin-left: auto;
}
.header__bottom .lang a {
  font-size: 13px;
  line-height: 1.6;
  color: #fff;
  font-weight: 500;
}
.header__bottom .lang a.current {
  color: #d9f1ad;
}
.header__bottom .lang a:not(:last-of-type) {
  position: relative;
  margin-right: 23px;
}
.header__bottom .lang a:not(:last-of-type):after {
  content: '';
  position: absolute;
  right: -14px;
  top: 0;
  width: 2px;
  height: 100%;
  background-color: #fff;
}
.header .mmenu {
  padding: 20px;
  position: fixed;
  right: -100%;
  top: 87px;
  width: 80%;
  z-index: 2;
  background-color: #007ac3;
