.main-cards {
  padding: 80px 0 100px;
}
.main-cards__wrap {
  display: flex;
  margin: 0 -15px;
}
.main-cards .item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: #f4f4f4;
  width: calc((100% / 3) - 30px);
  margin: 0 15px 30px;
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
  padding: 50px;
  padding-bottom: 40px;
}
.main-cards .item:after {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  width: 40px;
  height: 40px;
  background: linear-gradient(to left bottom, #fff 28px, #85bc20 0) no-repeat 100% 0 / 40px 40px;
  transition: 0.3s;
}
.main-cards .item:hover:after {
