.modal {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 40px 15px;
  z-index: 3;
  background-color: rgba(91, 91, 91, 0.75);
  overflow: auto;
}
.modal__wrap {
  display: flex;
  justify-content: center;
}
.modal .container {
  background-color: #fff;
  width: 100%;
  max-width: 700px;
}
.modal .head {
  display: flex;
  align-items: center;
  background-color: #007ac3;
  padding: 25px 35px 21px 40px;
}
.modal .head .name {
  font-size: 30px;
  line-height: 1.5;
  font-family: 'Cuprum', sans-serif;
  font-weight: 400;
  color: #fff;
  margin: 0;
  margin-right: 10px;
}
.modal .head .close {
  background: none;
  border: none;
  outline: none;
  padding: 0;
  margin-left: auto;
}
.modal .head .close svg {
  width: 22px;
  height: 22px;
  fill: #fff;
}
.modal .content {
  padding: 40px;
  padding-bottom: 20px;
}
.modal .tabs {
  display: flex;
  margin-bottom: 27px;
  border-bottom: 1px solid #bdbdbd;
}
.modal .tabs__item {
  font-size: 20px;
  font-family: 'Cuprum', sans-serif;
  line-height: 1.5;
  color: rgba(91, 91, 91, 0.4);
  padding: 8px 0;
  position: relative;
  cursor: pointer;
}
.modal .tabs__item:not(:last-of-type) {
  margin-right: 39px;
}
.modal .tabs__item:not(:last-of-type):before {
  content: '/';
  position: absolute;
  right: -23px;
  top: 8px;
  font-size: 20px;
  font-family: 'Cuprum', sans-serif;
  line-height: 1.5;
  color: rgba(91, 91, 91, 0.4);
}
.modal .tabs__item.active {
  color: #5b5b5b;
}
.modal .tabs__item.active:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 100%;
  height: 3px;
  background-color: #ea8f00;
}
.modal .tabs-content {
  display: none;
}
.modal .tabs-content.active {
  display: block;
}
.modal .attention {
  padding-bottom: 28px;
  border-bottom: 2px dotted #5b5b5b;
  margin-bottom: 30px;
}
.modal .attention .name {
  font-weight: 700;
  font-size: 20px;
  color: #ea8f00;
  margin-bottom: 11px;
